.recent-posts-showcase {
	display: grid;
	gap: 1rem;

	.swiper-pagination {
		position: relative;
		margin-top: 20px;
		display: block !important;
		opacity: 1 !important;
		text-align: center;
	}

	.recent-post-item {
		background: #fff;
		border: 1px solid #e5e7eb;
		border-radius: 0.5rem;
		box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
		padding: 1.25rem;
		transition: box-shadow 0.3s;

		.recent-post-thumbnail img {
			width: 100%;
			height: auto;
			border-radius: 0.375rem;
			margin-bottom: 1rem;
		}

		.recent-post-title {
			font-size: 1.125rem;
			font-weight: 600;
			margin-bottom: 0.5rem;

			a {
				color: #2563eb;
				text-decoration: none;

				&:hover {
					text-decoration: underline;
				}
			}
		}

		.recent-post-meta {
			font-size: 0.875rem;
			color: #6b7280;
			margin-bottom: 0.5rem;
		}

		.recent-post-excerpt {
			font-size: 0.9375rem;
			color: #374151;
		}
	}
}

.recent-posts-showcase.grid {
	grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.recent-posts-showcase.list {
	.recent-post-item {
		display: flex;
		gap: 2rem;
	}
}

.swiper-container {
	position: relative;
	overflow: hidden;
	width: 100%;

	.rps-swiper-pagination{
		margin-bottom: 20px;
	}
}